* {
  box-sizing: border-box;
}
body {
  font-family: 'Orbitron', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color:black;
  color: chartreuse;
}
#title {
  margin-top: 1rem;
  margin-bottom: 3rem;
}
#clock-frame {
  width: 520px;
  height: 500px;
  padding-top: 1rem;
  background-color: #FFFFE4;
  margin: 0 auto;
}
#wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#clock {
  background-color: black;
  width: 500px;
  height: 300px;
  border: 5px solid #d1d1b2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
button {
  border: 3px solid #d1d1b2;
  background-color: #FFFFE4;
}
button:active {
  background-color: #d1d1b2;
}
#time-left {
  font-size: 8rem;
}
#inputs {
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#start_stop {
  font-size: 1.5rem;
  width: 80%;
}
#reset {
  font-size: 1.5rem;
  width: 80%;
}
#break {
  width: 150px;
  display: flex;
  flex-direction: column;
}
#break-label {
  font-size: 1.5rem;
}
#session-label {
  font-size: 1.5rem;
}
#session {
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.button-wrapper-col {
  display: flex;
  row-gap: 0.5rem;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  width: 30%;
  height: 100%;
}
.button-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.small-screen {
  background-color: black;
  border: 3px solid #d1d1b2;
  width: 4.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
#break-length {
  font-size: 2rem;
}
#session-length {
  font-size: 2rem;
}
